import React from "react";
import * as UU5 from "uu5g04";
import Cfg from "../core/_config.js";

import Calls from "calls"

export default React.createClass({

  //@@viewOn:mixins
  mixins:[
    UU5.Common.BaseMixin,
    UU5.Common.ElementaryMixin,
    UU5.Common.RouteMixin,
    UU5.Common.LoadMixin
  ],
  //@@viewOff:mixins

  //@@viewOn:statics
  statics:{
    tagName:Cfg.APP + ".TractorDetail",
    classNames:{
      main:Cfg.CSS + "-tractor-detail"
    },
    calls:{
      onLoad:"findCars",

    }
  },

    componentWillMount(){
        this.setCalls(Calls)
    },


    _handleFilterClick(){
        this.setState({
            loadFeedback:"loading"
        }, () =>{
            this.getCall("find")({
                data:this._filterForm.getValues(),
                done:(data) =>{
                    this.setState({
                        dtoOut:data,
                        loadFeedback:"ready",
                        filtered:true
                    })
                },
                fail:(response) => console.error(response)
            })
        })
    },





    _handleLoadedTractorDetail(tractorDetail){
        if(!tractorDetail || tractorDetail.length === 0){
            return <UU5.Bricks.P>Není tu žádný detail</UU5.Bricks.P>
        }

        let lines = tractorDetail.map((detail) => (
            <UU5.Bricks.Table.Tr key={detail.id}>
              <UU5.Bricks.Table.Td>{detail.vin}</UU5.Bricks.Table.Td>
              <UU5.Bricks.Table.Td>
                <UU5.Bricks.DateTime format="dd.mm.Y" value={new Date(detail.checkDate)  } />
              </UU5.Bricks.Table.Td>


              <UU5.Bricks.Table.Td> {detail.destcription}</UU5.Bricks.Table.Td>
              <UU5.Bricks.Table.Td>{detail.isPass}</UU5.Bricks.Table.Td>
            </UU5.Bricks.Table.Tr>
        ));


        return (
            <UU5.Bricks.Table striped>
              <UU5.Bricks.Table.THead>
                <UU5.Bricks.Table.Tr>
                  <UU5.Bricks.Table.Th>Auto</UU5.Bricks.Table.Th>
                  <UU5.Bricks.Table.Th>Datum kontroly</UU5.Bricks.Table.Th>
                  <UU5.Bricks.Table.Th>Popis</UU5.Bricks.Table.Th>
                  <UU5.Bricks.Table.Th>Prošel</UU5.Bricks.Table.Th>
                </UU5.Bricks.Table.Tr>
              </UU5.Bricks.Table.THead>
              <UU5.Bricks.Table.TBody>
                  {lines}
              </UU5.Bricks.Table.TBody>
            </UU5.Bricks.Table>
        )
    },
    //@@viewOff:componentSpecificHelpers

    //@@viewOn:render
    render(){
        return (
            <UU5.Bricks.Div>
              <UU5.Bricks.Header level={2}>Detail vozidla</UU5.Bricks.Header>
              <UU5.Bricks.Panel header="Detail" alwaysExpanded={true} disableHeaderClick={true}>
                  {this.props.car.id}
                </UU5.Bricks.Panel>
            </UU5.Bricks.Div>
        );
    }
    //@@viewOff:render
});



